import React,{useState, useRef} from 'react';
import Header from '../Components/header';
import Footer from "../Components/footer"

const ContactUs = () => {
    let nameRef = useRef();
    let emailRef = useRef();
    let writeRef = useRef();
    let [write, setWrite] = useState("");
    let [btn_hide, set_btn_hide] = useState("");
    let [show_info, set_show_info] = useState('');

   let handleSubmit = (event) => {
       event.preventDefault();
       set_btn_hide("hide-btn");
       set_show_info("show-info")
       console.log(btn_hide)
   }
   let handleChange = (event) => {
       setWrite(event.target.value)
   }
    
    return <>
        <div style={{margin: "0px", padding: "0px", backgrounColor: "#eee"}}>
        <div className='cu-section'>
            <div className='cu-image'>
            <img src={`${process.env.PUBLIC_URL}/images/image1.jpg`} alt="image" />
            </div>
        </div>
        {/* contact form */}
        <div className='cu-section2'>
            <div className='cu-section2-inner'>
            <h2>Contact Form</h2>
            <form action="#" onSubmit={(event) => {handleSubmit(event)}}>
                <label htmlFor='name'>Name</label>
                <input type="text" name='name' id='name' ref={nameRef} />
                <label htmlFor='email'>Email</label>
                <input type="text" name='email' id='email' ref={emailRef} />
                <label></label>
                <label></label>
                <textarea name="write" id="" cols="30" rows="10" onChange={(event) => {handleChange(event)}} value={write}></textarea>
                <button type='submit' className={`btn ${btn_hide}`} >Submit</button>
                <h2 style={{display: "none"}} className={`${show_info}`}>We Will Contact You As Soon As Posible</h2>
            </form>
            </div>
        </div>
        {/* contact */}
        <div className='cu-section3'>
            <div className='cu-setion3-inner'>
            <h2>Call Now And Get Free Demo Class</h2>
            <p>Phone :</p><p>&nbsp;&nbsp;1234556778</p>
            </div>
        </div>
        {/* footer */}
        </div>
       
    </>
}
export default ContactUs;
