import { useState, useEffect, useRef } from "react";

let Imgs = ["image1.jpg","image2.jpg","image3.jpg"];
let ImgsArrayLength = Imgs.length-1;

const Slider = ({height,width}) => {
    const [index, setIndex] = useState(0);
    const timer = useRef();
    var h = height === undefined ? 88 : parseInt(height);
    var w = width === undefined ? 100 : parseInt(width);
    useEffect(()=> {
      clearInterval(timer.current)
      timer.current = setInterval(() => {
        (index === ImgsArrayLength) ? setIndex(0) : setIndex((pre) => { return pre+1 })
         clearInterval(timer.current);
      }, 7000)
      
    },[index])

    const handleSlide = (side) => {
      if (side === "right") {
            (index === ImgsArrayLength) ? setIndex(0) : setIndex((pre) => { return pre+1 })
            clearInterval(timer.current) 
      }
      if (side === "left") {
            (index === 0) ? setIndex(ImgsArrayLength) : setIndex((pre) => { return pre-1 })
            clearInterval(timer.current)
      }
    }
    return <div style={{overflow: "hidden"}}>
     <div className="slider-container" style={{height: `${h}vh`, width: `${w}vw`}}>

        <div className="left-arrow" onClick={() => {handleSlide("left")} }> <img src={`${process.env.PUBLIC_URL}/images/arrow.png`} style={{height: "30px", width: "30px", transform: "scaleX(-1)"}} alt="left-arrow" /> </div>
        <div className="right-arrow" onClick={() => {handleSlide("right")} }> <img src={`${process.env.PUBLIC_URL}/images/arrow.png`} style={{height: "30px", width: "30px"}} alt="right-arrow" /></div>

        <div className="slider-wrapper">
        <div className="slider" style={{transform: `translateX(${-(index * w)}vw)`}}>
            {Imgs.map((Imgs) => 
            <div className="slides" style={{height: `${h}vh` ,width: `${w}vw`}}> <img src={require(`../assets/${Imgs}`).default} alt={Imgs}/> </div> 
             )} 
        </div> 
        </div>
    </div>
    </div>
}
export default Slider;
