import React,{useState} from "react";

import { Link ,BrowserRouter as Router} from "react-router-dom"
import Logo from "../assets/new-logo.png" ;

const Header = () => {
    const [check, setCheck] = useState("hide-first");
    const handleCheck = () => {
      if (check === "hide-first") { setCheck("show-menu")
      } else if (check === "show-menu") { setCheck("hide-menu")
      }else if (check === "hide-menu") { setCheck("show-menu")} 

    }

    return <>
    <header>
        <div className="logo-text-container">
            <div className="logo"><img src={Logo} alt="logo"/></div>
            <h1>Gurukul Karnal</h1>
        </div>
        
            <ul className="navbar-item-container">
              
                <li className="navbar-item"> <Link to="/">Home</Link></li>
                <li className="navbar-item"> <Link to="contactus">Contact Us</Link></li>
                <li className="navbar-item"> <Link to="aboutus">About Us</Link></li>
             
            </ul>
            <div className="mobile-menu-icon">
                <img src={`${process.env.PUBLIC_URL}/images/hide.png`} height={"35px"} width={"35px"} alt="menu icon" onClick={()=> {handleCheck()}} />
            </div>
            <div style={{overflow: "hidden"}}>
            <div className={`mobile-menu ${check}`}>
            <ul className="mobile-menu-navbar-item-container">
                <li className="navbar-item">Home</li>
                <li className="navbar-item"><a href="">Contact Us</a> </li>
                <li className="navbar-item"><a href="">About Us</a></li>
            </ul>
            </div>
            </div>
    </header>
    
    </>
}
export default Header;